import * as React from "react"
import { Link } from "gatsby"
import Seo from "../components/seo"
import { Form, Button, InputGroup } from '@themesberg/react-bootstrap';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch, faEye } from "@fortawesome/free-solid-svg-icons";
import Parse from '../vendor/Parse'
import { useLocation } from '@reach/router';
import NumberTable from '../components/NumberTable'
import { Modal } from '@themesberg/react-bootstrap';
import useAuth from "../hooks/userAuth";
import NumberGrid from "../components/GridNumbers"
import { navigate } from 'gatsby';
const { useState, useEffect } = React;
const IndexPage = ({ type = 'numbers', title = 'Mobile Number Finding', renderComponent, onQuery }) => {

    const location = useLocation();
    const qs = new URLSearchParams(location.search)
    const q = qs.get('q')
    const p = qs.get('p')

    const [items, setItems] = useState([])
    const [query, setQuery] = useState(q || "")
    const [checklist, setChecklist] = useState([])
    const { isLogin, logout } = useAuth()
    const [displayInfo, setDisplayInfo] = useState(null)
    const [totalPages, setTotalPages] = useState(0)
    const [currentPage, setCurrentPage] = useState(Number(p) || 0)
    const [isLoading, setIsLoading] = useState(true)
    const [isSuggestionLoading, setSuggestionLoading] = useState(false)
    const [activeNumber, setActiveNumber] = useState(null)
    const [updateDate, setUpdateDate] = useState("")
    const [responseResult, setResponseResult] = useState(null)
    const searchNumbers = async (query, page = 0) => {
        setIsLoading(true)
        const res = await Parse.Cloud.run('searchNumbers', {
            query,
            page
        })
        setItems(res.result)
        setTotalPages(res.totalPage)
        setUpdateDate(res.date)
        setIsLoading(false)
        setResponseResult(res)
    }

    useEffect(() => {
        if (activeNumber) {
            setDisplayInfo(<><h2 className="text-center">{activeNumber?.num}</h2>
                {isLogin?.role === 'user' && <h2 className="text-center">Provider: {activeNumber?.sourceId}</h2>}
                {
                    renderComponent && renderComponent(activeNumber)
                }
            </>)
        }
    }, [activeNumber])

    const handleSearchButton = async () => {
        navigate(`/?q=${query?.trim()}&p=0`)
    }

    useEffect(() => {
        setCurrentPage(Number(p))
        searchNumbers(q, Number(p) || 0)
    }, [p, q])

    const handleItem = async (item) => {
        console.log(item)
        if (isLogin) {

        } else {
            navigate(`/register`)
        }

    }

    useEffect(() => {
        onQuery && onQuery(query)
    }, [query])

    return <>
        <div className="d-xl-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4 flex-column text-center" >

            {
                <NumberGrid
                    isLoading={isLoading}
                    data={items}
                    onItem={item => handleItem && handleItem(item?.item)}
                    totalPages={totalPages}
                    currentPage={currentPage}
                    prevLink={`/?q=${query}&p=${Math.max(currentPage - 1, 0)}`}
                    nextLink={`/?q=${query}&p=${Math.min(currentPage + 1, totalPages - 1)}`}
                />
            }

            {
                !isLoading && !items.length && <div style={{ marginTop: '16px' }}>
                    <Button
                        variant="outline-secondary"
                        className="w-100 py-3 px-4 fw-medium fs-5 bg-white text-dark border shadow-sm"
                        onClick={e => { navigate(`/enquire/?num1=${query}`) }}
                    >
                        Notify Me on {query}
                    </Button>
                </div>

            }

        </div>
    </>
}

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="Home" />

export default IndexPage
